.am-tab-bar {
  height: 100%;
  overflow: hidden;
}
.am-tab-bar-bar {
  position: relative;
  box-sizing: border-box;
  height: 3.125rem;
  border-top: 1PX solid #ddd;
  width: 100%;
  display: flex;
  transition-duration: .2s;
  transition-property: height bottom;
  z-index: 100;
  justify-content: space-around;
  align-items: center;
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  html:not([data-scale]) .am-tab-bar-bar {
    border-top: none;
  }
  html:not([data-scale]) .am-tab-bar-bar::before {
    content: '';
    position: absolute;
    background-color: #ddd;
    display: block;
    z-index: 1;
    top: 0;
    right: auto;
    bottom: auto;
    left: 0;
    width: 100%;
    height: 1PX;
    transform-origin: 50% 50%;
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  html:not([data-scale]) .am-tab-bar-bar::before {
    transform: scaleY(0.33);
  }
}
.am-tab-bar-bar-hidden-top {
  bottom: 3.125rem;
  height: 0;
}
.am-tab-bar-bar-hidden-bottom {
  bottom: -3.125rem;
  height: 0;
}
.am-tab-bar-bar .am-tab-bar-tab {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  width: 100%;
}
.am-tab-bar-bar .am-tab-bar-tab-image {
  width: 1.375rem;
  height: 1.375rem;
  vertical-align: middle;
}
.am-tab-bar-bar .am-tab-bar-tab-title {
  font-size: 0.625rem;
  margin: 0.1875rem 0 0 0;
  line-height: 1;
  text-align: center;
}
.am-tab-bar-bar .am-tab-bar-tab-icon {
  display: flex;
  justify-content: center;
}
.am-tab-bar-bar .am-tab-bar-tab-icon .tab-badge :last-child {
  margin-top: 0.25rem;
  left: 1.375rem;
}
.am-tab-bar-bar .am-tab-bar-tab-icon .tab-dot :last-child {
  margin-top: 0.25rem;
  left: 1.375rem;
}
.am-tab-bar-item {
  height: 100%;
}
