.am-icon {
  fill: currentColor;
  background-size: cover;
  width: 1.375rem;
  height: 1.375rem;
}
.am-icon-xxs {
  width: 0.9375rem;
  height: 0.9375rem;
}
.am-icon-xs {
  width: 1.125rem;
  height: 1.125rem;
}
.am-icon-sm {
  width: 1.3125rem;
  height: 1.3125rem;
}
.am-icon-md {
  width: 1.375rem;
  height: 1.375rem;
}
.am-icon-lg {
  width: 2.25rem;
  height: 2.25rem;
}
.am-icon-loading {
  -webkit-animation: cirle-anim 1s linear infinite;
          animation: cirle-anim 1s linear infinite;
}
@-webkit-keyframes cirle-anim {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes cirle-anim {
  100% {
    transform: rotate(360deg);
  }
}
